import * as React from 'react';
import { Link, NavLink } from 'react-router-dom';
import { AppBar, Box, Toolbar, IconButton, Typography, Menu, Container, Button, MenuItem, Avatar } from '@mui/material/';
import MenuIcon from '@mui/icons-material/Menu';
import ArrowDropDownRoundedIcon from '@mui/icons-material/ArrowDropDownRounded';
import logoImage from '../assets/logo.png';

const pages = [
  { label: 'Home', path: '/' },
  {
    label: 'Services', subMenu: [
      { label: 'Security Awareness Training', path: '/securityAwarenessTraining' },
      { label: 'Penetration Testing', path: '/penetrationTesting' },
      { label: 'Incident Response', path: '/incidentResponse' },
      { label: 'Security Audit', path: '/securityAudit' },
      { label: 'Courses', path: '/courses' },
    ]
  },
  { label: 'Pricing', path: '/pricing' },
  { label: 'Contact', path: '/contact' },
  { label: 'About', path: '/about' },
  { label: 'Courses', path: '/courses' },
  { label: 'News', path: '/news' },
];

function NavBar() {
  const [anchorElNav, setAnchorElNav] = React.useState(null);

  const [anchorEl, setAnchorEl] = React.useState({});

  const handleClick = (label, event) => {
    setAnchorEl(prevState => ({ ...prevState, [label]: event.currentTarget }));
  };

  const handleClose = (label) => {
    setAnchorEl(prevState => ({ ...prevState, [label]: null }));
  };

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  return (
    <AppBar position="sticky" sx={{ height: 56, boxShadow: 0, backgroundColor: '#000000' }}>
      <Container maxWidth="xl">
        <Toolbar sx={{ height: 56 }} disableGutters>
          <Avatar
            alt='logo'
            src={logoImage}
            sx={{
              display: { xs: 'none', md: 'flex' },
              mr: 0,
              width: 40,
              height: 40,
              borderRadius: '100px',
            }}
          />
          <Typography
            variant="h6"
            noWrap
            component={Link}
            to="/"
            sx={{
              mr: 0,
              display: { xs: 'none', md: 'flex' },
              fontFamily: '-apple-system, BlinkMacSystemFont',
              fontWeight: 600,
              letterSpacing: '0rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            Guardify
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
            <IconButton
              size="small"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
                paddingTop: '2px',
                paddingBottom: '2px',
                fontFamily: '-apple-system, BlinkMacSystemFont',
              }}
            >
              {pages.map((page) => (
                <React.Fragment key={page.label}>
                  <Button
                    component={NavLink}
                    to={page.path}
                    exact
                    sx={{
                      my: 0,
                      color: 'Black',
                      display: 'block',
                      fontFamily: '-apple-system, BlinkMacSystemFont',
                      fontWeight: '400',
                      fontSize: '15px',
                      textTransform: 'capitalize',
                      '&.active': { background: page.label === 'Services' ? 'transparent' : 'rgba(0, 0, 0, 0.1)' },
                    }}
                    activeClassName="active"
                    onMouseEnter={page.subMenu ? (event) => handleClick(page.label, event) : undefined}
                  >
                    {page.label}
                    {page.subMenu && <IconButton sx={{ padding: 0, color: 'Black' }}> <ArrowDropDownRoundedIcon /> </IconButton>}
                  </Button>
                  {page.subMenu && (
                    <Menu
                      anchorEl={anchorEl[page.label]}
                      open={Boolean(anchorEl[page.label])}
                      onMouseLeave={() => handleClose(page.label)}
                      onClose={() => handleClose(page.label)}
                    >
                      {page.subMenu.map((subItem) => (
                        <MenuItem
                          key={subItem.label}
                          component={NavLink}
                          to={subItem.path}
                          exact
                          sx={{
                            margin: '0px',
                            padding: '0px',
                            display: 'block',
                            fontFamily: '-apple-system, BlinkMacSystemFont',
                            textTransform: 'capitalize',
                            '&.active': { backgroundColor: 'lightgrey' },
                          }}
                          activeClassName="active"
                          onClick={() => handleClose(page.label)}
                        >
                          {subItem.label}
                        </MenuItem>
                      ))}
                    </Menu>
                  )}
                </React.Fragment>
              ))}
            </Menu>
          </Box>
          <Avatar
            alt='logo'
            src={logoImage}
            sx={{
              display: { xs: 'flex', md: 'none' },
              mr: 0,
              width: 40,
              height: 40,
              borderRadius: '100px',
            }}
          />
          <Typography
            variant="h5"
            noWrap
            component={Link}
            to="/"
            sx={{
              mr: 0,
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              fontFamily: '-apple-system, BlinkMacSystemFont',
              fontSize: '22px',
              fontWeight: 500,
              letterSpacing: '0rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            Guardify
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' }, alignItems: 'center', justifyContent: 'center' }}>
            {pages.map((page) => (
              <React.Fragment key={page.label}>
                <Button
                  component={NavLink}
                  to={page.path}
                  exact
                  sx={{
                    my: 2,
                    color: 'white',
                    display: 'block',
                    fontFamily: '-apple-system, BlinkMacSystemFont',
                    textTransform: 'capitalize',
                    '&.active': { background: page.label === 'Services' ? 'transparent' : 'rgba(255, 255, 255, 0.1)' },
                  }}
                  activeClassName="active"
                  onMouseEnter={page.subMenu ? (event) => handleClick(page.label, event) : undefined}
                >
                  {page.label}
                  {page.subMenu && <IconButton sx={{ padding: 0, color: 'whitesmoke' }}> <ArrowDropDownRoundedIcon /> </IconButton>}
                </Button>
                {page.subMenu && (
                  <Menu
                    anchorEl={anchorEl[page.label]}
                    open={Boolean(anchorEl[page.label])}
                    onMouseLeave={() => handleClose(page.label)}
                    onClose={() => handleClose(page.label)}
                  >
                    {page.subMenu.map((subItem) => (
                      <MenuItem
                        key={subItem.label}
                        component={NavLink}
                        to={subItem.path}
                        exact
                        sx={{
                          my: 0,
                          display: 'block',
                          fontFamily: '-apple-system, BlinkMacSystemFont',
                          textTransform: 'capitalize',
                          '&.active': { backgroundColor: 'lightgrey' },
                        }}
                        activeClassName="active"
                        onClick={() => handleClose(page.label)}
                      >
                        {subItem.label}
                      </MenuItem>
                    ))}
                  </Menu>
                )}
              </React.Fragment>
            ))}
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}

export default NavBar;
