import React, { useEffect, useState } from 'react';
import { Grid, Box, Typography, TextField, Button, Link, Card, CardContent } from '@mui/material';
import AppBar from '../NavBar';
import Footer from '../Footer';

const NewsPage = () => {
  const [news, setNews] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [searchTerm, setSearchTerm] = useState('');

  const itemsPerPage = 20;

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(
          `https://hacker-news.firebaseio.com/v0/newstories.json`
        );
        const storyIds = await response.json();

        // Set total pages based on all news articles
        setTotalPages(Math.ceil(storyIds.length / itemsPerPage));

        // Fetch news articles based on the current page
        const startIndex = (currentPage - 1) * itemsPerPage;
        const endIndex = startIndex + itemsPerPage;
        const limitedStoryIds = storyIds.slice(startIndex, endIndex);
        const storyPromises = limitedStoryIds.map((id) =>
          fetch(`https://hacker-news.firebaseio.com/v0/item/${id}.json`)
        );
        const stories = await Promise.all(storyPromises);
        const storyData = await Promise.all(stories.map((story) => story.json()));
        setNews(storyData);

        // Apply search filter
        const filteredNews = storyData.filter(
          (story) =>
            story.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
            (story.text && story.text.toLowerCase().includes(searchTerm.toLowerCase()))
        );
        setNews(filteredNews);

      } catch (error) {
        console.error('Error fetching news:', error);
      }
    };

    fetchData();
  }, [currentPage, searchTerm]);

  const handlePageChange = (newPage) => {
    if (newPage >= 1 && newPage <= totalPages) {
      setCurrentPage(newPage);
    }
  };

  const searchStyle = {
    margin: '20px 0',
    display: 'flex',
    justifyContent: 'center',
    backgroundColor: '#fff',
    borderRadius: '5px',
  };

  return (
    <>
      <AppBar />
      <Box sx={{ flexGrow: 1, padding: '20px', minHeight: '100vh', backgroundColor: '#000000', color: '#fff', }}>
        <Grid container spacing={2} justifyContent="center">
          <Grid item xs={12} md={8}>
            <Typography variant="h4" align="center" gutterBottom>
              Hacker News
            </Typography>
            <Box sx={searchStyle}>
              <TextField
                fullWidth
                label="Search News"
                variant="outlined"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </Box>
            {news.map((story) => (
              <Card key={story.id} elevation={0} sx={{backgroundColor: '#000000', color: '#fff', marginBottom: '30px'}}>
                <CardContent>
                  <Typography variant="h5">
                    <Link href={story.url} target="_blank" rel="noopener noreferrer" underline='hover'>
                      {story.title}
                    </Link>
                  </Typography>
                </CardContent>
              </Card>
            ))}
          </Grid>
        </Grid>
        <Box sx={{ display: 'flex', justifyContent: 'center', marginTop: '20px' }}>
          <Button
            variant="contained"
            color="primary"
            onClick={() => handlePageChange(currentPage - 1)}
            disabled={currentPage === 1}
          >
            Previous
          </Button>
          <Typography sx={{ margin: '0 15px' }}>
            Page {currentPage} of {totalPages}
          </Typography>
          <Button
            variant="contained"
            color="primary"
            onClick={() => handlePageChange(currentPage + 1)}
            disabled={currentPage === totalPages}
          >
            Next
          </Button>
        </Box>
      </Box>
      <Footer />
    </>
  );
};

export default NewsPage;
