import React, { useState, useEffect } from 'react';
import AppBar from '../NavBar';
import Footer from '../Footer';
import { Box, Typography, useTheme } from '@mui/material';
import CommingSoon from '../../assets/cmsoon.jpg';

function IncidentResponse() {
    const theme = useTheme();

    const [timeRemaining, setTimeRemaining] = useState(getTimeRemaining());

    useEffect(() => {
        const interval = setInterval(() => {
            setTimeRemaining(getTimeRemaining());
        }, 1000);

        return () => clearInterval(interval);
    }, []);

    function getTimeRemaining() {
        const targetDate = new Date('March 1, 2024 00:00:00').getTime();
        const now = new Date().getTime();
        const difference = targetDate - now;

        if (difference < 0) {
            return { days: 0, hours: 0, minutes: 0, seconds: 0 };
        }

        const days = Math.floor(difference / (1000 * 60 * 60 * 24));
        const hours = Math.floor((difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        const minutes = Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((difference % (1000 * 60)) / 1000);

        return { days, hours, minutes, seconds };
    }

    // Custom styles
    const styles = {
        pageContainer: {
            height: '100vh',
            backgroundImage: `url(${CommingSoon})`,
            backgroundSize: 'cover',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'space-between',
        },
        contentBox: {
            flexGrow: 1,
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            color: theme.palette.text.primary,
            backgroundColor: 'rgba(255, 255, 255, 0.2)',
            padding: theme.spacing(2),
            borderRadius: theme.spacing(2),
        },
        comingSoonText: {
            fontWeight: 'bold',
            marginBottom: theme.spacing(4),
        },
        countdown: {
            fontSize: '2.8rem',
            marginBottom: theme.spacing(4),
        },
        inspirationalQuote: {
            fontStyle: 'italic',
            marginBottom: theme.spacing(4),
        },
    };

    return (
        <>
            <AppBar />
            <Box sx={styles.pageContainer}>
                <Box sx={styles.contentBox}>
                    <Typography variant="h3" sx={[styles.comingSoonText, { [theme.breakpoints.down('sm')]: {fontSize: '2rem'}, justifyContent: 'center', textAlign: 'center', alignContent: 'center' }]}>
                        Incident Response Services - Coming Soon
                    </Typography>
                    <Typography variant="h5" sx={styles.countdown}>
                        {timeRemaining.days}d {timeRemaining.hours}h {timeRemaining.minutes}m {timeRemaining.seconds}s
                    </Typography>
                    <Typography variant="body1" sx={[styles.inspirationalQuote, { textAlign: 'center', justifyContent: 'center', alignContent: 'center' }]}>
                        "Prepare for the unexpected. Our Incident Response services are on the way."
                    </Typography>
                </Box>
            </Box>
            <Footer />
        </>
    );
}

export default IncidentResponse;
