import React, { useState } from 'react';
import { Box, Container, Typography, TextField, Button, Paper, useTheme, Snackbar } from '@mui/material';
import MuiAlert from '@mui/material/Alert';
import AppBar from '../NavBar';
import Footer from '../Footer';
import ContactUs from '../../assets/ContactUs.png';

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const ContactPage = () => {
  const theme = useTheme();
  const [formData, setFormData] = useState({ name: '', email: '', subject: '', message: '' });
  const [isFormSubmitted, setIsFormSubmitted] = useState(false);
  const [isFormSubmittedError, setIsFormSubmittedError] = useState(false);
  const [isErrorMessage, setIsErrorMessage] = useState(false);

  const handleFormSubmit = async (e) => {
    e.preventDefault();

    try {
//      const response = await fetch('https://guardifytech.site/contactCreate/', {
        const response = await fetch('http://localhost:8000/contactCreate/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });

      if (response.ok) {
        setIsFormSubmitted(true);

        document.cookie.split(";").forEach((c) => {
          document.cookie = c
            .replace(/^ +/, "")
            .replace(/=.*/, "=;expires=" + new Date().toUTCString() + ";path=/");
        });
      } else {
        const errorData = await response.json();
        console.error(errorData);
        setIsFormSubmittedError(errorData.deatil || 'Something went wrong!');
      }
    } catch (error) {
      console.error('Error submitting form:', error, 'Please try again.');
      setIsErrorMessage(error.message || 'Something went wrong! Please try again.');
    }
    setFormData((prevData) => ({ ...prevData, name: '', email: '', subject: '', message: '' }));
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({ ...prevData, [name]: value }));
  };

  const handleCloseSnackbar = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setIsFormSubmitted(false);
    setIsFormSubmittedError(false);
    setIsErrorMessage(false);
  };


  return (
    <>
      <AppBar />
      <Box className='main' component='main' sx={{ position: 'relative', minHeight: '100vh', backgroundImage: `url(${ContactUs})`, backgroundSize: 'cover', backgroundPosition: 'center', backgroundRepeat: 'no-repeat', }}>
        <Box
          className='overlay'
          sx={{
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(48, 25, 52, 0.3)',
          }}
        ></Box>
        <Paper
          className='content'
          elevation={0}
          sx={{
            position: 'absolute',
            top: '42%',
            left: '50%',
            width: '84%',
            transform: 'translate(-50%, -50%)',
            textAlign: 'center',
            justifyContent: 'center',
            padding: 3,
            backgroundColor: 'rgba(0, 0, 0, 0)',
            color: 'rgba(255, 255, 255, 1)',
          }}
        >
          <Snackbar
            anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
            open={isFormSubmitted}
            autoHideDuration={10000}
            onClose={handleCloseSnackbar}
          >
            <Alert onClose={handleCloseSnackbar} severity="success">
              Form submitted successfully!
            </Alert>
          </Snackbar>
          <Snackbar
            anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
            open={isFormSubmittedError}
            autoHideDuration={10000}
            onClose={handleCloseSnackbar}
          >
            <Alert onClose={handleCloseSnackbar} severity="warning">
              Declined by server to submit form!
            </Alert>
          </Snackbar>
          <Snackbar
            anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
            open={isErrorMessage}
            autoHideDuration={10000}
            onClose={handleCloseSnackbar}
          >
            <Alert onClose={handleCloseSnackbar} severity="error">
              {isErrorMessage}
            </Alert>
          </Snackbar>
          <Container sx={{ backgroundColor: 'rgba(0, 0, 0, 0)' }}>
            <Typography variant="h2" mt={4} mb={4} sx={{ [theme.breakpoints.down('sm')]: { fontSize: '3rem' } }}>
              Contact Us
            </Typography>
            <form onSubmit={handleFormSubmit}>
              <Box sx={{ mb: 3 }}>
                <TextField
                  fullWidth
                  label="Name"
                  variant="outlined"
                  placeholder="Your Name (required)"
                  id="name"
                  name="name"
                  type='text'
                  onChange={handleInputChange}
                  value={formData.name}
                  required
                  InputProps={{ style: { backgroundColor: 'rgba(255, 255, 255, 0.1)', color: 'white' } }}
                  InputLabelProps={{ style: { color: 'white' } }}
                />
              </Box>

              <Box sx={{ mb: 3 }}>
                <TextField
                  fullWidth
                  label="Email"
                  variant="outlined"
                  placeholder="Your Email (required)"
                  id="email"
                  name="email"
                  type="email"
                  onChange={handleInputChange}
                  value={formData.email}
                  required
                  InputProps={{ style: { backgroundColor: 'rgba(255, 255, 255, 0.1)', color: 'white' } }}
                  InputLabelProps={{ style: { color: 'white' } }}
                />
              </Box>

              <Box sx={{ mb: 3 }}>
                <TextField
                  fullWidth
                  label="Subject"
                  variant="outlined"
                  placeholder="Your Subject (required)"
                  id="subject"
                  name="subject"
                  type='text'
                  onChange={handleInputChange}
                  value={formData.subject}
                  required
                  InputProps={{ style: { backgroundColor: 'rgba(255, 255, 255, 0.1)', color: 'white' } }}
                  InputLabelProps={{ style: { color: 'white' } }}
                />
              </Box>

              <Box sx={{ mb: 3 }}>
                <TextField
                  fullWidth
                  label="Message"
                  variant="outlined"
                  placeholder="Your Message (required)"
                  id="message"
                  name="message"
                  type='text'
                  onChange={handleInputChange}
                  value={formData.message}
                  multiline
                  rows={5}
                  required
                  InputProps={{ style: { backgroundColor: 'rgba(255, 255, 255, 0.1)', color: 'white' } }}
                  InputLabelProps={{ style: { color: 'white' } }}
                />
              </Box>
              <Button type="submit" variant="contained" color="primary">
                Submit
              </Button>
            </form>
          </Container>
        </Paper>
      </Box>
      <Footer />
    </>
  );
};

export default ContactPage;
